<template>
	<div id="myInfo">
		<similarHeader title='个人信息' @goBack='goBack' :haveMore='false'></similarHeader>
		<ul class='list'>
			<li>
				头像
				<p>
					<img src="http://img2.imgtn.bdimg.com/it/u=3363472252,3522967664&fm=26&gp=0.jpg" alt="" @click='showImg=true'>
				</p>
			</li>
			<li @click='changeNames'>
				昵称
				<p style='width: 83.5%;'>{{myName}}</p>
			</li>
			<li class='noIcon'>
				微信号
				<p>
					Maple_Story
				</p>
			</li>
			<li>
				二维码名片
				<p class='widthHalf'>
					<img src="~img/images/my/erweima.png" alt="" class="erweima">
				</p>
			</li>
			<li class='border' @click.stop='showPop=true'>
				更多
			</li>
			<li>
				我的地址
			</li>
		</ul>
		<van-popup v-model="showPop"  position="bottom" :style="{ height: '24%' }" round :duration=0.2>
			<ul class='upop'>
				<li>
					从手机相册选择
				</li>
				<li>
					查看上一张头像
				</li>
				<li>
					保存到手机
				</li>
			</ul>
		</van-popup>
		<van-dialog
		  	v-model="showImg"
			:showConfirmButton='false'	
			:overlay='false'
			:duration=0.2
		>
			<div class='dialogTop'>
				<img src="~img/images/my/left.png" alt="" @click='showImg=false'  class='goBack'>
				<span>头像</span>
				<img src="~img/images/my/more.png" alt="" class='more' @click='Pop'>
			</div>
		    <img src="http://img2.imgtn.bdimg.com/it/u=3363472252,3522967664&fm=26&gp=0.jpg">
		</van-dialog>
		<van-popup
			  v-model="changeName"
			  position="right"
			  :style="{ height: '100%',width:'100%' }"
				:close-on-click-overlay='false'
				:overlay='false'
				:duration=0.2
			>
			<div class="changeName">
				<header class='similar'>	
					<img src="~img/images/chat/left.png" alt="" class='goBack' @click='leaveName'>
					<h2>更改名字</h2>
					<van-button :disabled='dis' type="danger" @click='saveName'>保 存</van-button>
				</header>
				<div class="changeCon">
					<van-field v-model="myName"  ref='names' size='large' @input='changeTxt'/>
				</div>
				<span>好名字可以让你的朋友更容易记住你。</span>
			</div>			
		</van-popup>
		
	</div>
</template>
<script>
	import  similarHeader from './similar-header'
	export default{
		data(){
			return{
				f:false,
				showImg:false,
				showPop:false,
				changeName:false,
				dis:true,
				myName:sessionStorage.getItem('myName'),
				avator:['http://img2.imgtn.bdimg.com/it/u=3363472252,3522967664&fm=26&gp=0.jpg']
			}
		},
		methods:{
			Pop(){
				this.showPop=true;
			},
			goBack(){
				this.$emit('isShow')
			},
			changeNames(){
				this.changeName=true;
			},
			changeTxt(){
				this.dis=false;
			},
			leaveName(){
				this.dis=true;
				this.changeName=false;
				this.myName=sessionStorage.getItem('myName');
			},
			saveName(){
				sessionStorage.setItem('myName',this.myName);
				this.$emit('cName',this.myName);
				this.leaveName();

			}
		},
		components:{
			similarHeader
		}
	}
</script>
<style scoped>
	#myInfo{
		height: 100vh;
		overflow: hidden;
		background: #ededed;
		font-size: .37rem;
		
	}
	ul.list{
		font-size: .38rem;
    	background: #fff;
	}

	ul.list li{
		padding: .3rem;
		margin-left: .28rem;
		padding-left: 0;
		border-bottom: 1px solid rgba(215,215,215,.3);
		background:url('~img/images/right.png') no-repeat 96% center/.3rem;
		display: flex;
		align-items: center;
	}
	ul.list li:first-child{
		padding: .15rem .15rem .15rem  0;
	}
	ul.list li p{
		text-align: right;
	    width: 82%;
	    display: inline-block;
	    color:#acacac;
	}
	ul li p img{
		width: 1.18rem;
		height: 1.18rem;
		border-radius: .16rem;
		vertical-align: middle;
	}
	ul.list li.noIcon{
		background:none;
	}
	ul.list li.border{
		border-bottom: .2rem solid #ededed;
	    margin-left: 0;
	    padding-left: .28rem;
	}
	ul.list img.erweima{
		width:.5rem;
    	height: .5rem;
    	
	}
	ul.list p.widthHalf{
		width: 67%;
	}
	.van-image-preview__index img{
		width: .7rem;
	    height: .7rem;
	    margin-left: .2rem;
	}
	#myInfo >>> .van-image-preview__index{
		display: flex;
		align-items: center;
		top:.1rem;
		font-size: .36rem;

		width: 100vw;
		transform: none;
		z-index: 9999;
		left:0;
	}
	.van-dialog {
		width:100%;
		height: 100vh;
		background-color:#000;
		color:#fff;
		overflow: initial;
		left: 0;
		top:0;
		transform: translate(0,0);
		border-radius: 0;
	}
	.van-image-preview__index .more{
		width: .56rem;
		height: .56rem;
		position: absolute;
		right: .17rem;
	}
	.van-dialog__content img{
		max-width: 100%;
		position: fixed;
    	top: 50%;
   		transform: translateY(-50%);
	}
	.van-dialog__content{
		text-align: center;
		height: 100%;
	}
	#myInfo >>> .van-overlay {
		height: 100vh;
	}
	#myInfo >>> .dialogTop{
		position: relative;
		top:.4rem;
		height: .7rem;
	    display: flex;
	    align-items: center;
	    font-size: .37rem;
	    margin:0 auto;
	}
	#myInfo >>> .dialogTop img{
		width: .75rem;
	    height: .75rem;
	    margin-top: 0;
	}
	.van-dialog__content img.goBack{
		margin-left: .1rem;
		left: .08rem;
	}
	#myInfo >>> .dialogTop img{
		position: absolute;
	}
	#myInfo >>> .dialogTop img.more{
	    right:.3rem;
	    width: .61rem;
	    height: .61rem;
	}
	#myInfo >>> .dialogTop span{
		position: absolute;
		left: .84rem;
	}
	#myInfo >>> .van-popup{
		transition: .17s;
		border-top-left-radius: .22rem;
    	border-top-right-radius: .22rem;
	}
	.upop li{
		padding: .26rem .2rem .26rem .4rem;
	}
	.changeName{
		height: 100vh;
		background:#ededed;
		text-align: center;
	}	
	.changeName header{
		border-bottom: 1px solid #ddd;
	}
	.changeName .van-button{
		position: absolute;
		height: 70%;
		right: .3rem;
		font-size: .35rem;
	}
	.changeName .van-cell{
		background: none;
		padding: .1rem;
	}
	.changeName >>> .van-cell__value--alone {
		margin: 0 .4rem;
    	border-bottom: 1px solid brown;
	}
	.changeName .van-button__text{
		display: flex;
		height: 100%;
		align-items: center;
	}
	.changeName >>>.van-button--normal {
		padding: 0 .28rem;
	}
	.changeCon{
		margin-top: .8rem;
	}
	.changeCon >>> .van-field__control {
		margin-bottom: .08rem;
		font-size: .34rem;
	}
	.changeName>span{
		color: #979797;
	    display: inline-block;
	    margin-top: .2rem;
	    font-size: .36rem;

	}
</style>


